import React from "react";
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import AttachmentButton from "./AttachmentButton";

function SolArch(){
    return (
    <main>
    <div className="container">
        <h2 className="title">SOLUTION ARCHITECT ASSUMPTIONS</h2>
        <form id="submit-form">
            <div className="comp" style={{marginTop: '8%'}}>
            <div className="drop">
                <label for="status">Area/Position</label>
                    <select id="slct" required>
                        <option value="" disabled selected>Select an Assumption</option>
                        <option value="">Assumption - 1</option>
                        <option value="">Assumption - 2</option>
                    </select>
                </div>
            <label htmlFor="" style={{marginTop: '10%'}}>Assumption</label>
            <Box
                sx={{
                    width: 700,
                    maxWidth: '100%',
                    marginTop: 2,
                    marginBottom: 2,
                }}
            >
            <TextField className="txt" fullWidth label="Enter your answer" id="fullWidth" multiline rows={4} />
            </Box>
            <label htmlFor="" style={{marginTop: '10%'}}>Description</label>
            <Box
                sx={{
                    width: 700,
                    maxWidth: '100%',
                    marginTop: 2,
                    marginBottom: 2,
                }}
            >
            <TextField className="txt" fullWidth label="Enter your answer" id="fullWidth" multiline rows={4}/>
            </Box>
            <div className="button-container">
            <input className="btn sum" type="submit" value="SUBMIT" style={{float: 'right'}}/>
            <AttachmentButton/>
            </div>
        </div>
    </form>
    </div>
</main>
    );
}

export default SolArch;