import React from "react";
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import AttachmentButton from "./AttachmentButton";

function SowCost(){

    return (
    <main>
    <div className="container">
        <h2 className="title">SOW COST</h2>
        <form id="submit-form">
            <div className="comp" style={{marginTop: '8%'}}>
            <label htmlFor="" style={{marginTop: '10%'}}>SOW Cost</label>
            <Box
                sx={{
                    width: 700,
                    maxWidth: '100%',
                    marginTop: 2,
                    marginBottom: 2,
                }}
            >
            <TextField className="txt" fullWidth label="Enter your answer" id="fullWidth" />
            </Box>
            <label htmlFor="">Spent</label>
            <Box
                sx={{
                    width: 700,
                    maxWidth: '100%',
                    marginTop: 2,
                    marginBottom: 2,
                }}
            >
            <TextField className="txt" fullWidth label="Enter your answer" id="fullWidth" />
            </Box>
            <label htmlFor="">Left Over</label>
            <Box
                sx={{
                    width: 700,
                    maxWidth: '100%',
                    marginTop: 2,
                    marginBottom: 2,
                }}
            >
            <TextField className="txt" fullWidth label="Enter your answer" id="fullWidth" />
            </Box>
            <label htmlFor="">Planned</label>
            <Box
                sx={{
                    width: 700,
                    maxWidth: '100%',
                    marginTop: 2,
                    marginBottom: 2,
                }}
            >
            <TextField className="txt" fullWidth label="Enter your answer" id="fullWidth" />
            </Box>
            <div className="button-container">
            <input className="btn sum" type="submit" value="SUBMIT" style={{float: 'right'}}/>
            <AttachmentButton/>
            </div>
        </div>
    </form>
    </div>
</main>
    );
}

export default SowCost;