import React from "react";
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import AttachmentButton from "./AttachmentButton";

function ArchDec(){
    return (
    <main>
    <div className="container">
        <h2 className="title">ARCHITECTURE DECISIONS</h2>
        <form id="submit-form">
            <div className="comp" style={{marginTop: '2%'}}>
            <div className="drop">
                <label for="status">AD ID</label>
                    <select id="slct" required>
                        <option value="" disabled selected>Select an Assumption</option>
                        <option value="">AD - 1</option>
                        <option value="">AD - 2</option>
                        <option value="">AD - 3</option>
                    </select>
                </div>
            <label htmlFor="" style={{marginTop: '10%'}}>Subject</label>
            <Box
                sx={{
                    width: 700,
                    maxWidth: '100%',
                    marginTop: 2,
                    marginBottom: 2,
                }}
            >
            <TextField className="txt" fullWidth label="Enter your answer" id="fullWidth"  />
            </Box>
            <label htmlFor="" style={{marginTop: '10%'}}>Options</label>
            <Box
                sx={{
                    width: 700,
                    maxWidth: '100%',
                    marginTop: 2,
                    marginBottom: 2,
                }}
            >
            <TextField className="txt" fullWidth label="Enter your answer" id="fullWidth" />
            </Box>
            <div style={{marginBottom: '2%'}}>
                <label htmlFor="" style={{marginTop: '10%'}}>Decision</label><br/>
                <input type="radio" value="" name="" style={{marginTop: '2%'}}/> Yes
                <input type="radio" value="" name="" style={{marginLeft: '5%'}} /> No
            </div>
            <label htmlFor="" style={{marginTop: '10%'}}>Rationale</label>
            <Box
                sx={{
                    width: 700,
                    maxWidth: '100%',
                    marginTop: 2,
                    marginBottom: 2,
                }}
            >
            <TextField className="txt" fullWidth label="Enter your answer" id="fullWidth" />
            </Box>
            <div className="button-container">
                <input className="btn sum" type="submit" value="SUBMIT" style={{float: 'right'}} />
                <AttachmentButton/>
            </div>
        </div>
    </form>
    </div>
</main>
    );
}

export default ArchDec;