import React from "react";
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import AttachmentButton from "./AttachmentButton";

function Tl(){

    return (
    <main>
    <div className="container">
        <h2 className="title">TIMELINE</h2>
        <form id="submit-form">
            <div className="comp" style={{marginTop: '8%'}}>
            <label htmlFor="" style={{marginTop: '10%'}}>Timeline</label>
            <Box
                sx={{
                    width: 700,
                    maxWidth: '100%',
                    marginTop: 2,
                    marginBottom: 2,
                }}
            >
            <TextField className="txt" fullWidth label="Enter your answer" id="fullWidth" />
            </Box>
            <div className="date-cont" style={{display: 'flex', flexDirection: 'column'}}>
                    <div className="date-inp" style={{marginBottom: '3%'}}>
                        <label htmlFor="">Start Date</label>
                        <input className="date" type="date" style={{marginTop: '2%', width: '100%'}}/>
                    </div>
                    <div className="date-inp" style={{marginBottom: '3%'}}>
                        <label htmlFor="">Planned End Date</label>
                        <input className="date" type="date" style={{marginTop: '2%', width: '100%'}} />
                    </div>
                    <div className="date-inp" style={{marginBottom: '3%'}}>
                        <label htmlFor="">Actual End Date</label>
                        <input className="date" type="date" style={{marginTop: '2%', width: '100%'}}/>
                    </div>
                </div>
            <div className="button-container">
            <input className="btn sum" type="submit" value="SUBMIT" style={{float: 'right'}}/>
            <AttachmentButton/>
            </div>
        </div>
    </form>
    </div>
</main>
    );
}

export default Tl;