import React from "react";
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import AttachmentButton from "./AttachmentButton";

function ResourcePlanning(){
    return (
    <main>
    <div className="container">
        <h2 className="title">RESOURCE PLANNING</h2>
        <form id="submit-form">
            <div className="comp" style={{marginTop: '8%'}}>
            <label htmlFor="" style={{marginTop: '10%'}}>Resources Planning</label>
            <Box
                sx={{
                    width: 700,
                    maxWidth: '100%',
                    marginTop: 2,
                    marginBottom: 2,
                }}
            >
            <TextField className="txt" fullWidth label="" id="fullWidth" />
            </Box>
            <label htmlFor="">Resources</label>
            <Box
                sx={{
                    width: 700,
                    maxWidth: '100%',
                    marginTop: 2,
                    marginBottom: 2,
                }}
            >
            <TextField className="txt" fullWidth label="Enter the Resources" id="fullWidth" />
            </Box>
            <div className="date-cont">
                    <div className="date-input">
                        <label htmlFor="">Start Date</label>
                        <input className="date" type="date" />
                    </div>
                    <div className="date-input1">
                        <label htmlFor="">End Date</label>
                        <input className="date" type="date" />
                    </div>
                    .
                </div>
            <div className="button-container">
            <input className="btn sum" type="submit" value="SUBMIT" style={{float: 'right'}}/>
            <AttachmentButton/>
            </div>
        </div>
    </form>
    </div>
</main>
    );
}

export default ResourcePlanning;