import React from "react";
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import AttachmentButton from "./AttachmentButton";

function RolesResp(){
    return (
    <main>
    <div className="container">
        <h2 className="title">PROJECT ROLES AND RESPONSIBILITIES</h2>
        <form id="submit-form">
            <div className="comp" style={{marginTop: '8%'}}>
            <div className="drop">
                <label for="status">Area/Position</label>
                    <select id="slct" required>
                        <option value="" disabled selected>Select option</option>
                        <option value="">Business/IT Stakeholders</option>
                        <option value="">Operations Stakeholders</option>
                        <option value="">Security</option>
                        <option value="">Cloud Architecture</option>
                        <option value="">Cloud Enablement</option>
                    </select>
                </div>
            <label htmlFor="" style={{marginTop: '10%'}}>Name/Role</label>
            <Box
                sx={{
                    width: 700,
                    maxWidth: '100%',
                    marginTop: 2,
                    marginBottom: 2,
                }}
            >
            <TextField className="txt" fullWidth label="Enter your answer" id="fullWidth" />
            </Box>
            <label htmlFor="" style={{marginTop: '10%'}}>Contact Number</label>
            <Box
                sx={{
                    width: 700,
                    maxWidth: '100%',
                    marginTop: 2,
                    marginBottom: 2,
                }}
            >
            <TextField className="txt" fullWidth label="Enter your answer" id="fullWidth"/>
            </Box>
            <div className="button-container">
            <input className="btn sum" type="submit" value="SUBMIT" style={{float: 'right'}}/>
            <AttachmentButton/>
            </div>
        </div>
    </form>
    </div>
</main>
    );
}

export default RolesResp;