import React from "react";
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import AttachmentButton from "./AttachmentButton";

function ProjObjectives(){
    return (
    <main>
    <div className="container">
        <h2 className="title">PROJECT OBJECTIVES</h2>
        <form id="submit-form">
            <div className="comp" style={{marginTop: '8%'}}>
            <label htmlFor="" style={{marginTop: '10%'}}>Specific goals the project intends to achieve or solve. These objectives should be measurable and aligned with the project's purpose</label>
            <Box
                sx={{
                    width: 700,
                    maxWidth: '100%',
                    marginTop: 2,
                    marginBottom: 2,
                }}
            >
            <TextField className="txt" fullWidth label="Enter your answer" id="fullWidth" multiline rows={6} />
            </Box>
            <div className="button-container">
            <input className="btn sum" type="submit" value="SUBMIT" style={{float: 'right'}}/>
            <AttachmentButton/>
            </div>
        </div>
    </form>
    </div>
</main>
    );
}

export default ProjObjectives;